import React, { useContext } from "react";
import logo from "../../../assets/logo.jpeg";
import { Link, useNavigate } from "react-router-dom";
import { PrivatePaths, PublicPaths } from "../../../routes/path";
import styles from "./styles.module.css";
import "../../../shared/button.css";
import { useClockOut, useGetAttendance } from "./hooks";
import { AuthContext } from "../../../context";

const Home = () => {
  const { data } = useGetAttendance();
  const navigate = useNavigate();
  const authCtx = useContext(AuthContext);

  const logout = () => {
    authCtx.logout();
    navigate("/");
  };

  const clockinHandler = () => {
    navigate(PrivatePaths.CLOCK_IN);
  };

  const clockOutHandler = () => {
    navigate(PrivatePaths.CLOCK_OUT);
  };

  return (
    <div className={styles.home}>
      <div className={styles.logo}>
        <img src={logo} alt="AMS Logo" />
      </div>
      <h1>Attendance</h1>
      <h1>Management</h1>
      <h1>System</h1>

      <div style={{ marginTop: "4rem" }} className="btnContainer">
        {data?.status === "ClockedIn" ? (
          <div>
            <button className="btn btnYellow" onClick={clockOutHandler}>
              Clock Out
            </button>
          </div>
        ) : (
          <div>
            <button className="btn btnPurple" onClick={clockinHandler}>
              Clock In
            </button>
          </div>
        )}

        <div>
          <button className="btn btnBlack" onClick={logout}>
            Log Out
          </button>
        </div>
      </div>
    </div>
  );
};

export default Home;
